import React from 'react';
import './_menu.scss';
import {Link} from "react-router-dom";

const Menu = () => {
  return (
    <nav>
      <ul className="menu">
        <li>
          <Link to="/cart">
            <p>О нас</p>
          </Link>
        </li>
        <li>
          <Link to="/suppliers">
            Поставщики
          </Link>
          <Link to="/delivery">
            Поставщики
          </Link>
          <Link to="/workingConditions">
            Условия работы
          </Link>
          <Link to="/contacts">
            Контакты и реквизиты
          </Link>
          <Link to="/api">
            API
          </Link>
        </li>
      </ul>
    </nav>
  )
}

export default Menu;