import React from 'react';
import {Link} from "react-router-dom";
import {Logo} from "../Buttons";
import './_header.scss';
import {Menu} from "../index";

const Header = () => {

  return (
    <div className="header">
      <div className="header__container">
        <Link className="logo" to="/GetChips">
          <Logo/>
        </Link>
        <a className="header__phone" href="tel:+79991234567">
          <p>8-800-700-18-63</p>
        </a>
        <Menu/>
        <Link className="personalAccount" to="/personalAccount">
          <p>Личный кабинет</p>
        </Link>
        <Link className="cart" to="/cart">
          <p>Cart</p>
        </Link>
      </div>
    </div>
  )
}

export default Header